import React, { useEffect, useState } from 'react'
import { createHashRouter, Navigate, RouterProvider } from 'react-router-dom'
import Home from '../Home/Home'
import Main from '../Main/Main'
import Login from '../Login/Login'
import Register from '../Register/Register'
import Notfound from '../Notfound/Notfound'
import Movies from '../Movies/Movies'
import TvShows from '../TvShows/TvShows'
import jwtDecode from 'jwt-decode'
import ProtectedRoute from '../ProtectedRoute/ProtectedRoute'
import ItemDetails from '../ItemDetails/ItemDetails'


export default function App() {

  const [userData, setUserData] = useState(null)

  function saveUserData() {
    let encodedToken = localStorage.getItem('userToken')
    let decodedToken = jwtDecode(encodedToken);
    setUserData(decodedToken)
  }
  useEffect(() => {
    if(localStorage.getItem('userToken')!== null)
    {
      saveUserData()
    }
  }, [])
  
  function logOut()
  {
    localStorage.removeItem('userToken');
    setUserData(null);
    return <Navigate to='/login'/>
  }

  const router = createHashRouter([
    {
      path: '', element: <Main logOut={logOut} userData={userData}/>, children: [
        { index: true, element: <ProtectedRoute saveUserData={saveUserData} userData={userData}><Home/></ProtectedRoute>},
        { path: 'movies', element: <ProtectedRoute saveUserData={saveUserData} userData={userData}><Movies/></ProtectedRoute>},
        { path: 'tv-shows', element:  <ProtectedRoute saveUserData={saveUserData} userData={userData}><TvShows/></ProtectedRoute>},
        { path: 'movie-details/:id/:media_type', element:  <ProtectedRoute saveUserData={saveUserData} userData={userData}><ItemDetails/></ProtectedRoute>},
        { path: 'login', element: <Login saveUserData={saveUserData} /> },
        { path: 'register', element: <Register /> },
        { path: '*', element:  <ProtectedRoute saveUserData={saveUserData} userData={userData}><Notfound/></ProtectedRoute>}
      ]
    }
  ])
  return <>
    <RouterProvider router={router} />
  </>
}