/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Loading from '../Loading/Loading'
import Notfound from '../Notfound/Notfound'
import './ItemDetails.css'

export default function ItemDetails() {
  const { id, media_type } = useParams()
  const [idError, setIdError] = useState('');
  const imgPath = 'https://image.tmdb.org/t/p/w500';
  const [Details, setDetails] = useState([]);
  async function getItemDetails() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}?api_key=f1aca93e54807386df3f6972a5c33b50&language=en-US`).catch(error=>
    {
      setIdError(error.response.status)
    })
    setDetails(data)
  }
  useEffect(() => {
    getItemDetails()
  }, [])

  return <>

    {idError === 404 ? <Notfound/>:Details.length !== 0 ?    <><div style={{
      backgroundImage: `url("https://image.tmdb.org/t/p/original${Details.backdrop_path}")`
    }} className="container-fluid p-0 cover  animate__animated animate__fadeIn">
      <div className="cursorlay"><span  class="cursorDown"></span></div>
    </div><div className="container pt-5">
        <div className="row">
          <div className="col-md-4">
            <a href={Details.homepage} target='_blank' rel="noreferrer"><img className='img-fluid mt-3' src={imgPath + Details.poster_path} alt="" /></a>
            <h6 className='text-center'>{Details.genres ? Details.genres.map((gen, index) => <span key={index} className="badge bg-black p-2 px-2 mx-1 mt-2">{gen.name}</span>) : null}</h6>
          </div>
          <div className="col-md-8">
            <h1 className='text-center mt-3'>{Details.title ? Details.title : Details.name}</h1>
            <h4><span className="badge bg-success p-2 px-2 mx-1 mt-2">{Details.status}</span></h4>
            <p className='mt-3'>{Details.overview}</p>
            <hr />
            <div className="row">
              <div className="col-md-5">
                <h6>Release Date: {Details.release_date ? Details.release_date : Details.last_air_date}</h6>
                {Details.vote_average ? <h6>vote Average: {Details.vote_average}</h6> : null}
                {Details.vote_count ? <h6>vote Count: {Details.vote_count}</h6> : null}
                {Details.popularity ? <h6>Popularity: {Details.popularity}</h6> : null}
                <hr />
                {Details.production_companies ? <h6>Production: <span title='Company'>{Details.production_companies[0].name}</span> <span title='Country'> ({Details.production_companies[0].origin_country})</span></h6> : null}
              </div>
              <div className="col-md-7">
                {Details.production_companies ? <img className='img-fluid' src={imgPath + Details.production_companies[0].logo_path} alt="" /> : null}
              </div>
            </div>
          </div>
        </div>
      </div></>:<Loading />}

  </>
}
