import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import Loading from '../Loading/Loading';
import css from '../Movies/Movies.module.css'

export default function Movies() {
    const [trending, setTrending] = useState([]);

    async function getTrendingMovies() {
        let { data } = await axios.get(`https://api.themoviedb.org/3/trending/tv/week?api_key=f1aca93e54807386df3f6972a5c33b50`)
        setTrending(data.results)
    }
    useEffect(() => {
        getTrendingMovies()
    }, [])

    return <>
        <div className="container pt-5 animate__animated animate__fadeIn">
        <div className="row">
                {trending.length > 0 ? <div className="col-md-4 d-flex align-items-center">
                    <div className={css.trending}>
                        <h5 className='mt-5'>Trending TV Shows to watch now</h5>
                        <p className="text-muted">Lorem ipsum dolor sit amet consectetur adipisicing.</p>
                    </div>
                </div> : null}
                {trending.length > 0 ? trending.map((movie, index) =>
                    <div key={index} className='col-md-2 mb-3'>
                        <Link to={`/movie-details/${movie.id}/${movie.media_type}`}>
                            <div>
                                <img className='img-fluid' src={`https://image.tmdb.org/t/p/w500` + movie.poster_path} alt="" />
                            </div>
                        </Link>
                    </div>
                ) : <Loading />}
            </div>
        </div>
    </>
}
